"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { patientsApi } from "@/lib/api-client"
import { type Patient } from "@/lib/queries"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Header } from "@/components/header"
import { LoadingSpinner } from "@/components/loading-spinner"
import { AlertCircle, Users, Search } from "lucide-react"

export function PatientListPage() {
  const [patients, setPatients] = useState<Patient[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [searchTerm, setSearchTerm] = useState("")

  useEffect(() => {
    const fetchPatients = async () => {
      setLoading(true)
      setError(null)

      const result = await patientsApi.getAll()

      if (result.error) {
        setError(result.error)
      } else if (result.data) {
        setPatients(result.data)
      }

      setLoading(false)
    }

    fetchPatients()
  }, [])

  const filteredPatients = patients.filter(
    (patient) =>
      patient.fullName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      patient.firstName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      patient.lastName.toLowerCase().includes(searchTerm.toLowerCase()),
  )

  return (
    <div className="min-h-screen bg-background">
      <Header />

      <main className="container mx-auto px-4 py-8">
        {/* Page Header */}
        <div className="mb-8">
          <div className="flex items-center gap-3 mb-2">
            <div className="p-2 bg-primary/10 rounded-lg">
              <Users className="h-6 w-6 text-primary" />
            </div>
            <h1 className="text-3xl font-bold">Patient Directory</h1>
          </div>
          <p className="text-muted-foreground">Comprehensive patient database with medical records management</p>
        </div>

        {/* Search Card */}
        <Card className="mb-6 border-primary/20">
          <CardHeader>
            <CardTitle className="text-lg">Search Patients</CardTitle>
            <CardDescription>Find patients by name or ID</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="relative max-w-md">
              <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search by patient name..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10"
              />
            </div>
          </CardContent>
        </Card>

        {/* Error Alert */}
        {error && (
          <Card className="border-destructive/50 bg-destructive/5 mb-6">
            <CardContent className="flex gap-3 pt-6">
              <AlertCircle className="h-5 w-5 text-destructive flex-shrink-0 mt-0.5" />
              <div>
                <p className="font-semibold text-destructive">Error loading patients</p>
                <p className="text-sm text-destructive/80">{error}</p>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Content */}
        {loading ? (
          <div className="flex justify-center py-12">
            <LoadingSpinner />
          </div>
        ) : (
          <>
            <div className="mb-4 text-sm text-muted-foreground">
              Showing {filteredPatients.length} of {patients.length} patient{patients.length !== 1 ? "s" : ""}
            </div>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              {filteredPatients.length > 0 ? (
                filteredPatients.map((patient) => <PatientCard key={patient.id} patient={patient} />)
              ) : (
                <Card className="col-span-full">
                  <CardContent className="pt-12 text-center">
                    <Users className="h-12 w-12 text-muted-foreground/30 mx-auto mb-4" />
                    <p className="text-muted-foreground mb-4">
                      {searchTerm ? "No patients match your search" : "No patients found"}
                    </p>
                    {searchTerm && (
                      <Button variant="outline" onClick={() => setSearchTerm("")}>
                        Clear search
                      </Button>
                    )}
                  </CardContent>
                </Card>
              )}
            </div>
          </>
        )}
      </main>
    </div>
  )
}

function PatientCard({ patient }: { patient: Patient }) {
  const getDiagnosisBadgeColor = (diagnosis: string | null) => {
    if (!diagnosis) return "outline"
    const lower = diagnosis.toLowerCase()
    if (lower.includes("critical") || lower.includes("severe")) return "destructive"
    if (lower.includes("warning") || lower.includes("alert")) return "default"
    return "secondary"
  }

  return (
    <Link href={`/patient/${patient.id}`}>
      <Card className="h-full hover:border-primary hover:shadow-lg transition-all cursor-pointer group">
        <CardHeader>
          <div className="flex justify-between items-start gap-2">
            <div className="flex-1">
              <CardTitle className="text-xl group-hover:text-primary transition-colors">{patient.fullName}</CardTitle>
              <CardDescription className="mt-1">Age: {patient.age} years</CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <p className="text-xs text-muted-foreground font-semibold mb-2 uppercase tracking-wide">Last Diagnosis</p>
            {patient.lastDiagnosis ? (
              <Badge variant={getDiagnosisBadgeColor(patient.lastDiagnosis) as any} className="text-xs">
                {patient.lastDiagnosis}
              </Badge>
            ) : (
              <p className="text-sm text-muted-foreground italic">No diagnosis recorded</p>
            )}
          </div>

          <div className="pt-3 border-t border-border">
            <p className="text-xs text-muted-foreground">
              Member since <span className="font-medium">{new Date(patient.createdAt).toLocaleDateString()}</span>
            </p>
          </div>

          <Button className="w-full mt-2" size="sm">
            View Details â†’
          </Button>
        </CardContent>
      </Card>
    </Link>
  )
}
